/* Feedback Section
--------------------------------------------------------------------------------------*/
fieldset {
    h3 {
        text-align: center;
    }

    span:nth-of-type(9) {
        perspective: 300rem;
    }

    .teacher_feedback {
        perspective: 300rem;
        opacity: 0;

        label {
            display: block;
            transform-origin: bottom;
        }

        select {
            transform-origin: bottom;
            font-size: 1em;
            border: none;
        }
    }


    #BTNsu {
        padding: 5px;
        font-size: 1em;
        border: none;
        overflow: hidden;
        background-color: $footerbg;

        position: relative;
        z-index: 1;
    }

    #BTNsu::before {
        content: "";
        width: 100%;
        height: 30px;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        background-color: $headerbg;

        transform: translate3d(0px, -30px, 0px);
        transform-origin: top center;

        transition-property: transform;
        transition-duration: 0.3s;
        transition-delay: 0s;
        transition-timing-function: ease-out;
    }

    #BTNsu:hover,
    #BTNsu:focus {
        color: #fff;
    }

    #BTNsu:hover:before,
    #BTNsu:focus:before {
        transform: translate3d(0px, 0px, 0px);
    }

    .input-group {
        position: relative;
        perspective: 300rem;

        // label {
        //     position: absolute;
        //     top:0;
        //     left:0;
        //     font-size: 0.8em;
        //     transform-origin: top;
        //     transform: translate3d(0px, 0px, 0px);
            
        //     transition-property: transform, font-size;
        //     transition-duration: 0.5s;
        //     transition-delay: 0s;
        //     transition-timing-function: ease-in-out;
        // }

        input {
            border: none;
            outline: none;
            border-bottom: 1px solid #000;
            background-color: transparent;
        }

        label {
            position: absolute;
            top: 0;
            left: 0;
            font-size: 0.8em;
            transform-origin: top;
            transform: translate3d(0px, 0px, 0px);

            transition-property: transform, font-size;
            transition-duration: 0.5s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }

        input:valid + label,
        input:focus + label {
            transform: translate3d(0px, -18px, 0px);
            font-size: 0.6em;
        }

        // input:valid + label,
        // input:focus + label {
        //     transform: translate3d(0px, -18px, 0px);
        //     font-size: 0.6em;   
        // }
    }
}

/* Animation Section
--------------------------------------------------------------------------------------*/
@keyframes teacherFeedback {
    to {
        transform: translate3d(0px, 80px, 0px);
        opacity: 0.1;
    }
}

/* Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 18em) {
    fieldset {
        display: grid;
        grid-template-columns: 16em;
        grid-template-rows: repeat(10, auto);
        grid-row-gap: 1.5em;
        justify-content: center;
        border: none;

        hgroup {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        span:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        span:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        span:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 4/5;
        }

        span:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 5/6;
        }

        span:nth-of-type(5) {
            grid-column: 1/2;
            grid-row: 6/7;
        }

        span:nth-of-type(6) {
            grid-column: 1/2;
            grid-row: 7/8;
        }

        span:nth-of-type(7) {
            grid-column: 1/2;
            grid-row: 8/9;
        }

        span:nth-of-type(8) {
            grid-column: 1/2;
            grid-row: 9/10;
            margin: 0 auto;

            textarea {
                width: 20em;
                background-color: $cardsbg;
            }
        }

        span:nth-of-type(9) {
            grid-column: 1/2;
            grid-row: 10/11;
            margin: 0 auto;
        }

        #BTNsu {
            min-width: 150px;
            max-width: 300px;
        }
    }
}

@include breakpoints(min-width, 45em) {
    fieldset {
        grid-template-columns: repeat(2, 20em);
        grid-template-rows: repeat(9, auto);

        hgroup {
            grid-column: 1/3;
            grid-row: 1/2;
        }

        span:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        span:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        span:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 4/5;
        }

        span:nth-of-type(4) {
            grid-column: 2/3;
            grid-row: 4/5;
        }

        span:nth-of-type(5) {
            grid-column: 1/2;
            grid-row: 5/6;
        }

        span:nth-of-type(6) {
            grid-column: 2/3;
            grid-row: 5/6;
        }

        span:nth-of-type(7) {
            grid-column: 1/2;
            grid-row: 6/7;
        }

        span:nth-of-type(8) {
            grid-column: 1/3;
            grid-row: 7/8;

            textarea {
                width: auto;
            }
        }

        span:nth-of-type(9) {
            grid-column: 1/3;
            grid-row: 9/10;
        }

        #BTNsu {
            width: 300px;
        }
    }
}